import React, { Component } from 'react';
import {connect} from 'react-redux';

import {DragDropContext} from 'react-beautiful-dnd';

import EffectContainer from './containers/EffectContainer';
import {moveSound} from './actions/effectsActions';

class App extends Component {
  constructor(props){
    super(props);

    this.onDragEnd = this.onDragEnd.bind(this);
  }

  onDragEnd(result){
    console.log(result);
    const {destination, source, draggableId} = result;
    if(destination === null){return;}
    if(destination.droppableId === source.droppableId){
      this.props.moveSound(destination.droppableId, draggableId, source.index, destination.index);
    }
  }
  render() {
    return (
      <div className="App container">
        <DragDropContext onDragEnd={this.onDragEnd}>
          <EffectContainer/>
        </DragDropContext>
      </div>
    );
  }
}

const mapStateToProps = (state) =>{
  return{
    effects: state.effects,
    sounds: state.sounds
  }
}

const mapDispatchToProps = (dispatch) =>{
  return{
    moveSound: (effectId, soundId, oldIndex, newIndex) =>{
      dispatch(moveSound(effectId, soundId, oldIndex, newIndex));
    },
    displaceSound: (oldEffectId, newEffectId, soundId, index) =>{

    }
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(App);



